.ProductCardContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ProductCardImg {
    max-height: 40vh;
    width: auto;
    filter: brightness(0.8);
    opacity: 0.9;
}

.ProductCardName {
    font-weight: 800;
    color: #979797;
    font-size: 2rem;
}

.ProductCardPrice {
    font-weight: 600;
    color: #979797;
    font-size: 1.5rem;
}