.Checkout {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.CheckoutContainer {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.CheckoutTitle {
    font-weight: 800;
    color: #979797;
    font-size: 2rem;
}

.CheckoutImg {
    overflow: hidden;
    opacity: 0.7;
    filter: brightness(.7);
    border: 2px solid #FF9344;
    border-radius: 12px;
    width: 30%;
    margin: 6px 0 12px;
}

.CheckoutSubTitle {
    font-weight: 600;
    color: #979797;
    font-size: 1rem;
}